import sizes from "./sizes";

export default {
    Palette: {
        height: '100vh',
        display: 'flex',
        flexDirection: 'column'
    },
    paletteColors: {
        height: '90%'
    },
    goBack: {
        width: '20%',
        height: '50%',
        margin: '0 auto',
        display: 'inline-block',
        position: 'relative',
        cursor: 'pointer',
        marginBottom: '-4px',
        opacity: '1',
        backgroundColor: 'black',
        '& a': {
            width: '100px',
            height: '30px',
            position: 'absolute',
            display: 'inline-block',
            top: '50%',
            left: '50%',
            marginTop: '-15px',
            marginLeft: '-50px',
            textAlign: 'center',
            outline: 'none',
            background: 'rgba(255, 255, 255, 0.3)',
            fontSize: '1rem',
            lineHeight: '30px',
            color: 'white',
            textTransform: 'uppercase',
            border: 'none',
            textDecoration: 'none'
        },
    // [sizes.down('lg')]: {
    //     width: '50%',
    //     height: '33.333%'',
    // },
        [sizes.down('md')]: {
            width: '50%',
            height: '20%',
        },
        [sizes.down('xs')]: {
            width: '100%',
            height: '10%',
        }
    }
};